.container {
	padding: 0px 20px;
	max-width: 1220px;
	margin: 0 auto;
}
html, body {
	font-family: 'Noto Sans', sans-serif;
}

.main {
	padding-top: 81px;
	padding-bottom: 611px;
}
.main__title {
	font-weight: 700;
	font-size: 60px;
	line-height: 70px;
	text-align: center;
	color: #FFFFFF;
	z-index: 5;
	position: relative;
}
.vector-wrapper {
	position: relative;
	max-width: 706px;
	margin: 0 auto;
}
.main__vector {
	position: absolute;
	left: 324px;
	top: 67.5px;
	z-index: 1;
}
.main__text {
	max-width: 867px;
	margin: 0 auto;
	font-size: 20px;
	line-height: 32px;
	text-align: center;
	color: #C6C0C0;
	padding-top: 28px;
	padding-bottom: 48px;
}
.main__form {
	position: relative;
	width: 493px;
	margin: 0 auto;
	transition: all 0.3s ease 0s;
}
.main__form input {
	background: rgba(222, 222, 222, 0.2);
	border: 2px solid rgba(255, 255, 255, 0.4);
	border-radius: 45px;
	height: 64px;
	max-width: 493px;
	padding: 20px 0px 19px 24px;
	width: 493px;
	overflow: hidden;
	transition: all 0.3s ease 0s;
}
.main__form input::placeholder {
	font-size: 18px;
	line-height: 25px;
	color: #C6C0C0;
}
.main__button {
	border: none;
   margin: 0;
   padding: 0;
   width: auto;
   overflow: visible;
   background: transparent;
   /* inherit font & color from ancestor */
   color: inherit;
   font: inherit;
   /* Normalize `line-height`. Cannot be changed from `normal` in Firefox 4+. */
   line-height: normal;
   /* Corrects font smoothing for webkit */
   -webkit-font-smoothing: inherit;
   -moz-osx-font-smoothing: inherit;
   /* Corrects inability to style clickable `input` types in iOS */
   -webkit-appearance: none;
	position: absolute;
	top: 8px;
	right: 8px;
	cursor: pointer;
}
.main__button img {
	padding: 12px;
	background: #F08C00;
	border-radius: 50%;
	transition: all 0.5s ease 0.1s;
}
.main__button:hover .main__button-image  {
	background: #a25e00;
	transform: rotate(-360deg);
}

