.image {
	position: relative;
}
.image img {
	width: 100%;
	position: absolute;
	left: 0;
	top: 0;
	z-index: -1;
}

.image-elipse {
	position: absolute;
	left: 410px;
	top: 875px;
	cursor: pointer;
}
.second-elipse {
	position: absolute;
	left: 1236px;
	top: 1062px;
}
.image-elipse:hover span:last-child {
	width: 30px;
	height: 30px;
	left: 6px;
	top:  6px;
}
.image-elipse span:first-child{
	display: block;
	width: 40px;
	height: 40px;
	background: rgba(196, 196, 196, 0.5);
	border: 1px solid #FFFFFF;
	border-radius: 50%;
}
.image-elipse span:last-child {
	transition: all 0.3s ease 0s;
	display: block;
	width: 10px;
	height: 10px;
	border-radius: 50%;
	background-color: #fff;
	position: absolute;
	left: 16px;
	top: 16px;
}
