}
.card-koleksi__elipse {
	display: flex;
	flex-direction: column;
	align-items: center;
	position: relative;
	cursor: pointer;
	transition: all 0.3s ease 0s;
}
.card-koleksi__elipse:hover {
	transform: scale(1.1);
}
.card-koleksi__elipse span:first-child {
	display: block;
	width: 24px;
	height: 24px;
	background-color: #fff;
	border-radius: 50%;
	transition: all 0.3s ease 0s;
}
.card-koleksi__elipse:hover  span:first-child {
	background-color: #565050;
}
.card-koleksi__elipse span:nth-child(2) {
	position: absolute;
	left: 3px;
	top: 3px;
	display: block;
	width: 18px;
	height: 18px;
	background-color: #F08C00;
	border-radius: 50%;
}
.card-koleksi__elipse .svg-checked {
	position: absolute;
	left: 6px;
	top: -10px;
	opacity: 0;
	transition: all 0.3s ease 0s;
}

.card-koleksi__elipse:nth-child(1) span:nth-child(2) {
	background: #375C6C;
}
.card-koleksi__elipse:nth-child(3) span:nth-child(2) {
	background: #2C8234;
}

.checked span:first-child {
	background-color: #565050;
}
.checked .svg-checked {
	opacity: 1;
	top: 6px;
}
