.header {
	padding: 53px 0px;
}
.header__container {
	display: flex;
	align-items: center;
	justify-content: space-between;
}
.header__logo {

}
.header__menu {

}
.menu__body {

}
.menu__list {
	display: flex;
	gap: 60px;
}
.menu__item {

}
.menu__link {
	text-decoration: none;
	font-size: 18px;
	font-weight: 500;
	font-size: 18px;
	line-height: 25px;
	color: #C6C0C0;
	transition: all 0.3s ease 0s;
}
.menu__link:hover {
	color: #F08C00;
}
.header__cart {
	
}
.cart {
	cursor: pointer;
	transition: all 0.3s ease 0s;
}
.cart:hover {
transform: scale(1.2);
}
.cart:hover .cart__elipse {
	transform: scale(1.5);
}


.cart__image {
	position: relative;
}
.cart__elipse {
	left: 17px;
	top: -2px;
	position: absolute;
	transition: all 0.3s ease 0s;
}

.cart__elipse span:first-child{
	display: block;
	width: 16px;
	height: 16px;
	border-radius: 50%;
	background-color: #fff;
}
.cart__elipse span:nth-child(2){
	position: absolute;
	top: 2px;
	left:2px;
	display: block;
	width: 12px;
	height: 12px;
	border-radius: 50%;
	background-color: #F08C00;
}
