
.footer {
	padding: 40px 0px;
	margin-top: 100px;
}
.footer__container {
	display: flex;
	justify-content: space-between;
}
.footer__list {
	display: flex;
	gap: 24px;
}
.footer__link {
	line-height: 22px;
	text-decoration-line: underline;
	color: #8C8585;
}
@media (max-width: 420px){
	.footer__container {
		flex-direction: column-reverse;
		align-items: center;
		gap: 20px;
	}
}